<!-- 投注记录 -->
<template>
  <van-nav-bar left-arrow fixed title="投注记录" @click-left="handleBack">
    <template #right>
      <img src="../assets/images/kf.png" @click="openLayer = true" />
    </template>
  </van-nav-bar>
  <div class="container main-content" ref="container">
    <div class="filter-cont">
      <div class="cond-item" v-for="(item, index) in filterList" :key="index" @click="handlePicker(index)">
        <label>{{ item.title }}</label>
        <div class="box">
          {{ item.data ? item.data.text : '全部' }}
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
    <div class="cord-cont">
      <van-row class="head-cont">
        <van-col span="4">币种</van-col>
        <van-col span="5">投注金额</van-col>
        <van-col span="5">输赢结果</van-col>
        <van-col span="10">下注时间</van-col>
      </van-row>
      <van-list v-model:loading="loading" :finished="finished" :style="'height:' + height + 'px'" :finished-text="finished && datalist.length === 0 ? '暂无投注记录' : ''" @load="onLoad">
        <!-- <van-cell  :title="item" /> -->
        <van-row v-for="item in datalist" :key="item">
          <van-col span="4">{{ item.token }}</van-col>
          <van-col span="5">{{ item.betNum }}</van-col>
          <van-col span="5" :style="'color:' + item.color">{{ item.resultNum }}</van-col>
          <van-col span="10">{{ item.realTime }}</van-col>
        </van-row>
      </van-list>
    </div>
  </div>
  <van-popup v-model:show="showLayer" position="bottom">
    <van-picker :title="'请选择' + layerTitle" :columns="columns" @confirm="onConfirm" @cancel="onCancel" />
  </van-popup>
  <van-popup v-model:show="openLayer" closeable round>
    <service />
  </van-popup>
</template>

<script>
import store from '../store';
import { formatTime } from '../utils/formatTime';
import Service from '../components/Service.vue';
import { gameList } from '../utils/resources';
export default {
  name: 'RecoedList',
  components: { Service },
  data() {
    return {
      show: false,
      pg: 0,
      ps: 20,
      loading: false,
      finished: false,
      showLayer: false,
      filterList: [
        {
          title: "时间", name: "时间", key: "time", data: null, child: [
            // { text: '全部', id: 0, value: '' },
            { text: '1天内', id: 1, value: '' },
            { text: '3天内', id: 3, value: '' },
            { text: '7天内', id: 7, value: '' },
          ]
        },
        {
          title: "币种", name: '币种', key: "value_token", data: null, child: [
            { text: '全部', id: 0, value: ['usdt', 'trx'] },
            { text: 'USDT', id: 1, value: ['usdt'] },
            { text: 'TRX', id: 2, value: ['trx'] }
          ]
        },
        {
          title: "游戏", name: '游戏类型', key: "value_inaddress", data: null, child: [
          ]
        },
      ],
      columns: [],
      layerTitle: '',
      pickerIndex: -1,
      datalist: [],
      height: 0,
      openLayer: false
    }
  },
  created() {
    // this.getGameList();
    let list = gameList.map((it, index) => {
      return { text: it.name, id: index, value: [it.address] }
    })
    list.unshift({ text: '全部', id: 0, value: [] });
    this.filterList[2].child = list;
    let timeList = this.filterList[0].child;
    timeList.forEach(it => {
      if (it.id > 0) {
        it.value = this.getTimeStr(it.id);
      }
    })
    this.filterList[0].data = timeList[0];
  },
  mounted() {
    let total = window.innerHeight;
    let { container } = this.$refs;
    this.height = total - container.clientHeight;
  },
  methods: {
    handleBack() {
      this.$router.back();
    },
    getTimeStr(num) {
      let date = new Date();
      let day = date.getDate();
      day -= num;
      date.setDate(day);
      return formatTime(date, "", "-")
    },
    getGameList() {
      this.get("api/get_games.json").then(res => {
        this.filterList[1].child = res.list.map(it => {
          return { text: it.name }
        });
      })
    },
    onLoad() {
      let { token, wallet_address } = store.getters;
      if (!token || !wallet_address) this.showLogin = true;
      let param = {};
      this.filterList.forEach(({ key, data, child }) => {
        let it = data || child[0];
        let value = it.value;
        if (key === "time" && it.id > 0) {
          value = [it.value, formatTime(new Date(), '', '-')]
        }
        param[key] = value;
      });
      this.pg++;
      this.$toast.loading({ title: '加载中...', duration: 0, overlay: true });
      this.post("get_user_all", {
        limit: this.pg, size: this.ps,
        screen: {
          address: wallet_address,
          ...param
        }
      }, false).then(res => {
        this.$toast.clear()
        let rate = 1000000;
        this.loading = false;
        let list = res.data || [];
        list.forEach(it => {
          let time = new Date(it.time);
          time.setHours(time.getHours() + 8);
          it.realTime = formatTime(time, '', '-');
          it.resultNum = (it.out_num / rate).toFixed(2);
          it.betNum = (it.num / rate).toFixed(2);
          it.color = it.resultNum > 0 ? '#15e31b' : '#ff4444'
        })
        if (list.length < this.ps) this.finished = true;
        this.datalist = this.datalist.concat(list);
      }).catch(() => {
        this.finished = true;
      })
    },
    handlePicker(i) {
      let item = this.filterList[i];
      if (!item) return;
      this.pickerIndex = i;
      this.columns = item.child;
      this.layerTitle = item.name;
      this.showLayer = true
    },
    onConfirm(item) {
      this.filterList[this.pickerIndex].data = item;
      this.showLayer = false;
      this.pg = 0;
      this.finished = false;
      this.datalist = [];
    },
    onCancel() {
      this.showLayer = false
    }
  }
}
</script>
<style lang='scss' scoped>
.van-nav-bar {
  &:after {
    border: none;
  }
}

.container {
  // height: 100%;
  padding-bottom: 10px;
  box-sizing: border-box;

  .filter-cont {
    display: flex;
    padding: 0 16px;
    flex-wrap: wrap;
    align-items: center;
    font-size: 14px;

    .cond-item {
      width: 49%;
      display: flex;
      align-items: center;
      margin-right: 2%;
      margin-top: 10px;

      &:nth-child(2n) {
        margin-right: 0;
      }

      label {
        margin-right: 5px;
      }

      .box {
        flex: 1;
        line-height: 40px;
        padding: 0 5px;
        position: relative;
        border: 1px solid #f1f4ff;

        .van-icon-arrow {
          position: absolute;
          top: 6px;
          right: 5px;
          color: #999;
          font-size: 16px;
        }
      }

      &:nth-child(1),
      &:nth-child(2) {
        margin-top: 0;
      }
    }
  }

  .cord-cont {
    padding-top: 10px;

    .van-row {
      font-size: 14px;
      border-bottom: 1px solid #f1f1f1;

      .van-col {
        padding: 10px 0;
        text-align: center;
      }
    }

    .head-cont {
      color: #fff;
      font-size: 16px;
      background-color: var(--main-color);
    }
  }

  .van-list {
    overflow-y: auto;
    height: calc(100% - 70px);
  }
}

@media screen and(max-width:700px) {
  .container {
    .filter-cont {
      font-size: 12px;

      .cond-item {
        .box {
          line-height: 34px;
        }
      }
    }

    .cord-cont {
      .van-row {
        font-size: 12px;
      }
    }
  }
}
</style>